            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogBatteryVisible = false">{{$t('global.cancel')}}</el-button>
            </div>
        </el-dialog>
        <vnc-dialog ref="vncDialog">
        </vnc-dialog>
    </div>    
</template>
<script>

import handleResponse from "../../components/restfulapi/handleResponse";
import {setDeviceStatus as setDeviceStatusByPlugins , getDeviceStatus as getDeviceStatusByPlugins} from "../../components/restfulapi/pluginsApi";
import {getDeviceStatus, setDeviceStatus, getScreenshotImg} from '../restfulapi/deviceStatusApi'
import {pluginname} from "../../assets/js/constant";
import  {deviceDetail, deviceMonitor, appControl} from '../../assets/js/lwm2mMap'
import {androiddmServerIp, androiddmServerPort} from "../../assets/js/config"
import {getDeviceDetailApi} from '../restfulapi/deviceApi'
import { clearInterval } from 'timers'
import selectDevice from '../../common/select-device'
import vncDialog from './vncDialog'
import Chart from 'chart.js'
import {setSession, getSession} from "../../assets/js/storage"
import {mapState} from 'vuex'
import {groupType} from "../../assets/js/constant"
import {dockerAction} from '@/assets/js/lwm2mMap'

export default {
    name: 'devicestatus',
    data() {
        return {
            isNotClick: false,
            cpu_temp: "",
            cpu_usage: "",
            memory_total: "",
            memory_free: "",
            memory_usage: "",
            storage_total: "",
            storage_free: "",
            storage_usage: "",
            battery_available: "",
            cputemcolor: 'black',
            cpuusagecolor: 'black',
            memoryusagecolor: 'black',
            storageusagecolor: 'black',
            batterycolor: 'black',
            cputem: false,
            cputemwarn: 0,
            cputemerror: 0,
            cpuusage: false,
            cpuusagewarn: 0,
            cpuusageerror: 0,
            memoryusage: false,
            memoryusagewarn: 0,
            memoryusageerror: 0,
            storageusage: false,
            storageusagewarn: 0,
            storageusageerror: 0,
            battery: false,
            batterywarn: 0,
            batteryerror: 0,
            battery_health: "",
            battery_temp: "",
            batteryInfo: false,
            battery_health_color:"black",
            appList: [],
            peripheralList: [],
            deviceName: "",
            gname: "",
            endpoint: "",
                form: {
                    agentVersion: '',
                    systemVersion: '',
                    boardName: '',
                },
            cpuData: new Array(7),
            memoryData: new Array(7),
            cpuNowPercentage: 0,
            memoryNowPercentage: 0,
            cupChart: null,
            intervalTime: "5",
            dialogDetailsVisible: false,
            dialogBatteryVisible: false,
            screenshotImg:"",
            dialogAppList: [],
            dialogBatteryList: [],
            loading: false,
            cid: "",
            totalTime: 15,
            os: "",
            groupType,
            processTable: [],
            dockerTable: [],
            dockerLoading: false,
            processLoading: false,
            appLoading: false,
            
        }
    },

    methods: {
        countDown(){
            let clock = window.setInterval(() => {
            this.totalTime --;
            if(this.totalTime == 0 || this.dialogDetailsVisible == false){
                window.clearInterval(clock);
                this.isNotClick = false;
                this.totalTime = 15;
            }
            }, 1000)
        },
        refresh(){
            this.clickDetails(this.cid);
            this.isNotClick = true;
        },
        getDeviceThresholdSetting(){
            let param="{'get_threshold': true}"
            getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                handleResponse(data, (res) => {
                    if(res.status === "CONTENT"){
                        let thresholdInfo = JSON.parse(res.content.value);
                        if(thresholdInfo){
                            if(thresholdInfo.cpu_temp){
                                if(thresholdInfo.cpu_temp.status === 'true'){
                                    this.cputem = true;
                                }else{
                                    this.cputem = false;
                                }
                                this.cputemwarn = thresholdInfo.cpu_temp.warn;
                                this.cputemerror = thresholdInfo.cpu_temp.error;
                            }
                            if(thresholdInfo.cpu_usage){
                                if(thresholdInfo.cpu_usage.status === 'true'){
                                    this.cpuusage = true;
                                }else{
                                    this.cpuusage = false;
                                }
                                this.cpuusagewarn = thresholdInfo.cpu_usage.warn;
                                this.cpuusageerror = thresholdInfo.cpu_usage.error;
                            }
                            if(thresholdInfo.memory_usage){
                                if(thresholdInfo.memory_usage.status === 'true'){
                                    this.memoryusage = true;
                                }else{
                                    this.memoryusage = false;
                                }
                                this.memoryusagewarn = thresholdInfo.memory_usage.warn;
                                this.memoryusageerror = thresholdInfo.memory_usage.error;
                            }
                            if(thresholdInfo.storage_usage){
                                if(thresholdInfo.storage_usage.status === 'true'){
                                    this.storageusage = true;
                                }else{
                                    this.storageusage = false;
                                }
                                this.storageusagewarn = thresholdInfo.storage_usage.warn;
                                this.storageusageerror = thresholdInfo.storage_usage.error;
                            }
                            if(thresholdInfo.battery_available){
                                if(thresholdInfo.battery_available.status === 'true'){
                                    this.battery = true;
                                }else{
                                    this.battery = false;
                                }
                                this.batterywarn = thresholdInfo.battery_available.warn;
                                this.batteryerror = thresholdInfo.battery_available.error;
                            }
                        }                    
                    }
                })
                this.getBoardInfo();  
            }) 
        },
        getBoardInfo(){
            let param = "{'get_board_info':true}";
            getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                handleResponse(data, (res) => {
                    if(res.status === "CONTENT"){
                        let boardInfo = JSON.parse(res.content.value);
                        if(this.cputem&& boardInfo.cpu.temp!="N/A"){
                            if(parseInt(boardInfo.cpu.temp) < parseInt(this.cputemwarn)){
                                this.cputemcolor = "normal";
                            }else if(parseInt(boardInfo.cpu.temp) > parseInt(this.cputemerror)){
                                this.cputemcolor = "error";
                            }else{
                                this.cputemcolor = "warn";
                            }
                        }else{
                            this.cputemcolor = "black";
                        }
                        if(this.cpuusage&& boardInfo.cpu.usage!="N/A"){
                            if(parseInt(boardInfo.cpu.usage) < parseInt(this.cpuusagewarn)){
                                this.cpuusagecolor = "normal";
                            }else if(parseInt(boardInfo.cpu.usage) > parseInt(this.cpuusageerror)){
                                this.cpuusagecolor = "error";
                            }else{
                                this.cpuusagecolor = "warn";
                            }
                        }else{
                            this.cpuusagecolor = "black";
                        }
                        if(this.memoryusage&& boardInfo.memory.usage!="N/A"){
                            if(parseInt(boardInfo.memory.usage) < parseInt(this.memoryusagewarn)){
                                this.memoryusagecolor = "normal";
                            }else if(parseInt(boardInfo.memory.usage) > parseInt(this.memoryusageerror)){
                                this.memoryusagecolor = "error";
                            }else{
                                this.memoryusagecolor = "warn";
                            }
                        }else{
                            this.memoryusagecolor = "black";
                        }
                        if(this.storageusage&& boardInfo.storage.usage!="N/A"){
                            if(parseInt(boardInfo.storage.usage) < parseInt(this.storageusagewarn)){
                                this.storageusagecolor = "normal";
                            }else if(parseInt(boardInfo.storage.usage) > parseInt(this.storageusageerror)){
                                this.storageusagecolor = "error";
                            }else{
                                this.storageusagecolor = "warn";
                            }
                        }else{
                            this.storageusagecolor = "black";
                        }
                        if(this.battery&& boardInfo.battery.available!="N/A"){
                            if(parseInt(boardInfo.battery.available) > parseInt(this.batterywarn)){
                                this.batterycolor = "normal";
                            }else if(parseInt(boardInfo.battery.available) < parseInt(this.batteryerror)){
                                this.batterycolor = "error";
                            }else{
                                this.batterycolor = "warn";
                            }
                        }else{
                            this.batterycolor = "black";
                        }
                        this.cpu_temp = boardInfo.cpu.temp;
                        this.cpu_usage = boardInfo.cpu.usage;
                        this.memory_total = boardInfo.memory.total;
                        this.memory_free = boardInfo.memory.free;
                        this.memory_usage = boardInfo.memory.usage;
                        this.storage_total = boardInfo.storage.total;
                        this.storage_free = boardInfo.storage.free;
                        this.storage_usage = boardInfo.storage.usage;
                        if(boardInfo.battery.available=='N/A'||boardInfo.battery.available == undefined){
                            this.batteryInfo = false;
                        }else{
                            this.batteryInfo = true;
                        }
                        this.battery_available = boardInfo.battery.available? boardInfo.battery.available: "N/A";
                        this.battery_health = boardInfo.battery.health? boardInfo.battery.health: "N/A";
                        if(this.battery_health=='N/A'){
                            this.battery_health_color = 'black';
                        }else if(this.battery_health=='good'){
                            this.battery_health_color = 'normal';
                        }else if(this.battery_health=='dead'){
                            this.battery_health_color = 'error';
                        }else{
                            this.battery_health_color = 'warn';
                        }
                        this.battery_temp = boardInfo.battery.temperature? boardInfo.battery.temperature: "N/A";
                        this.cpuNowPercentage = boardInfo.cpu.usage;
                        this.memoryNowPercentage = boardInfo.memory.usage;
                        this.memoryData.push(this.memoryNowPercentage);
                        this.cpuData.push(this.cpuNowPercentage);
                        this.memoryData.shift();
                        this.cpuData.shift();
                        this.drawCpuChart();
                        this.drawMemoryChart();
                    }
                })
            })
        },
        getMonitorApps(){
            let param="{'get_monitor_app':true}";
            getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                handleResponse(data, (res) => {
                    if(res.status === "CONTENT"){
                        let tableArray = JSON.parse(res.content.value);
                        if(Object.prototype.toString.call(tableArray) === "[object Array]"){
                            if(this.os == groupType.linux|| this.os ==groupType.windows){
                                this.processTable = tableArray;
                            }else{
                                this.appList = tableArray;
                            }
                        }
                        
                    }
                })
            })
        },

        getMonitorDockers(){
            let param="{'get_monitor_docker':true}";
            getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                handleResponse(data, (res) => {
                    if(res.status === "CONTENT"){
                        let dockerData = JSON.parse(res.content.value);
                        if(Object.prototype.toString.call(dockerData) === "[object Array]"){
                            this.dockerTable = dockerData;
                        } 
                    }
                })
            })
        },
        getPeripherals(){
            let param="{'get_usb_status':true}";
            getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                handleResponse(data, (res) => {
                    if(Object.prototype.toString.call(res) === "[object Array]"){
                        this.peripheralList = res;
                    }else{
                        this.peripheralList = [];
                    }
                })
            })
        },
        deletePeripheral(usb){
            _g.swalWarnDo(this.$t('global.areYouSure'), this.$t('global.delete') , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                if (willfunc) {
                    let actions = {'del_usb_info': usb};
                    _g.openGlobalLoading();
                    let param={
                        pluginname: pluginname,
                        actions: actions
                    }
                    setDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() =>{
                                    this.getPeripherals();  
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                        
                    })

                }
            })
        },
        killProcess(processId){
            _g.swalWarnDo(this.$t('global.areYouSure'), this.$t('global.delete') , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                if (willfunc) {
                    let actions = {'kill_process': processId};
                    _g.openGlobalLoading();
                    let param={
                        pluginname: pluginname,
                        actions: actions
                    }
                    setDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() =>{
                                    this.getMonitorApps();  
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                        
                    })

                }
            })
        },
        stopApk(command){
            let cid=command.id;
            let setSensorVal = command.appInfo;
            _g.swalWarnDo(this.$t('global.areYouSure'), this.$t('overview.close') , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                if (willfunc) {
                    _g.openGlobalLoading();
                    setDeviceStatus(this.endpoint, appControl[cid], setSensorVal).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() =>{
                                    this.getMonitorApps();
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                        
                    })

                }
            })
        },

        startApk(command){
            let cid=command.id;
            let setSensorVal = command.appInfo;
            _g.swalWarnDo(this.$t('global.areYouSure'), this.$t('overview.start') , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                if (willfunc) {
                    _g.openGlobalLoading();
                    setDeviceStatus(this.endpoint, appControl[cid], setSensorVal).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() =>{
                                    this.getMonitorApps();
                                })
                            }else{
                                _g.handleError(res)
                            }
                        })
                        
                    })
                }
            })
        },

        initDetailsInfo(){
            this.dialogDetailsVisible = false;
            this.dialogBatteryVisible = false;
            this.dialogAppList = [];
            this.dialogBatteryList = [];
            this.loading = true;
            this.cid = "";
        },

        clickDetails(cid){
            if(cid=='cpu'){
                this.dialogAppList = [];
                this.loading = true;
                this.cid = cid;
                let param="{'get_cpuLoading_top5':true}";
                getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                    this.loading = false;
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let appList = JSON.parse(res.content.value);
                            if(this.os == groupType.linux|| this.os ==groupType.windows){
                                let processData = [];
                                appList.forEach((item) => {
                                    let processItem = {cmd: item.threadcmd, value: item.threadcpuloading}
                                    processData.push(processItem);
                                    
                                })
                                
                                this.dialogAppList = processData;
                            }else{
                                let processData = [];
                                for(var index in appList){
                                    let app ={cmd: index, value: appList[index]};
                                    processData.unshift(app);
                                }
                                this.dialogAppList = processData;
                            }
                            
                        }
                    })
                })
                this.dialogDetailsVisible = true;
            }else if(cid=='memory'){
                this.dialogAppList = [];
                this.loading = true;
                this.cid = cid;
                let param="{'get_memoryLoading_top5':true}";
                getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                    this.loading = false;
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let appList = JSON.parse(res.content.value);
                            if(this.os == groupType.linux|| this.os ==groupType.windows){
                                let processData = [];
                                appList.forEach((item) => {
                                    let processItem = {cmd: item.threadcmd, value: item.threadmemloading}
                                    processData.push(processItem);
                                    
                                })
                                
                                this.dialogAppList = processData;
                            }else{
                                let processData = [];
                                for(var index in appList){
                                    let app ={cmd: index, value: appList[index]};
                                    processData.unshift(app);
                                }
                                this.dialogAppList = processData;
                            }
                        }
                    })
                })
                this.dialogDetailsVisible = true;
            }else if(cid=='battery'){
                this.dialogBatteryList = [];
                this.loading = true;
                this.cid = cid;
                let param="{'get_battery_info':true}";
                getDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                    this.loading = false;
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                           this.dialogBatteryList.push(JSON.parse(res.content.value));
                        }
                    })
                })
                this.dialogBatteryVisible = true;
            }
        },

        getDeviceDetails(agentId){
            Object.assign(this.$data.form, this.$options.data().form)
            for(let key in deviceDetail){
                getDeviceDetailApi(agentId, deviceDetail[key]).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            this.form[key] = res.content.value;
                        }
                        
                    })
                    
                })
            }
            
        },

        execDevice(cid){
            switch (cid){
                case "kvm":
                    this.$refs.vncDialog.selectedAgentId = this.endpoint;
                    this.$refs.vncDialog.deviceName = this.deviceName;
                    this.$refs.vncDialog.vncDialogVisible = true;
                    break;
                case "screenshot":
                    this.screenShot();
                    break;
                case "poweroff":
                    this.action({shutdown:true}, 'shutdown');
                    break;
                case "reboot":
                    this.action({reboot:true}, 'reboot');
                    break;
                case "threshold":
                    setSession("mode", false);
                    setSession("selectedDeviceGroup", this.gname);
                    setSession("selectedAgentId", this.endpoint);
                    this.$router.push({
                        path: "/main/setting/setting",
                        query: {
                            activename: "thresholdSetup",
                        }
                    })
                    break;
                case "software":
                    let activename = ""
                    setSession("mode", false);
                    setSession("selectedDeviceGroup", this.gname);
                    setSession("selectedAgentId", this.endpoint);
                    this.$router.push({
                        path: "/main/setting/setting",
                        query: {
                            activename: "softwareMonitor",
                        }
                    })
                    break;
                 case "process":
                    setSession("mode", false);
                    setSession("selectedDeviceGroup", this.gname);
                    setSession("selectedAgentId", this.endpoint);
                    this.$router.push({
                        path: "/main/setting/setting",
                        query: {
                            activename: "processMonitor",
                        }
                    })
                    break;     
                 case "docker":
                    setSession("mode", false);
                    setSession("selectedDeviceGroup", this.gname);
                    setSession("selectedAgentId", this.endpoint);
                    this.$router.push({
                        path: "/main/setting/setting",
                        query: {
                            activename: "dockerMonitor",
                        }
                    })
                    break;     
                case "install":
                    setSession("mode", false);
                    setSession("selectedDeviceGroup", this.gname);
                    setSession("selectedAgentId", this.endpoint);
                    this.$router.push({
                    path: "/main/control/appcontrol",
                    query: {
                        activename: "appMarket",
                        }
                    })
                    break;     
            }
        },

        action(actions, cid){
            if(this.endpoint == ''){
                this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() =>{
                    this[cid] = this.$options.data()[cid];
                })
                return;
            }
            _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("remoteControl."+cid) , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                if (willfunc) {
                    _g.openGlobalLoading();
                    let param={
                        pluginname: pluginname,
                        actions: actions
                    }
                    setDeviceStatusByPlugins(this.endpoint, param).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                if(this.gname){
                                    this.$router.push({
                                    path: "/main/overview/overview",    
                                    })
                                }else {
                                    this.$router.push("/main/device/list");
                                }    
                            }else{
                                _g.handleError(res)
                            }
                        })
                    })
                }
            })    
        },

        actionContainer(info, cid){
             let containername = info.name;
             let version = info.id;
            if(dockerAction[cid] === undefined){
                this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                return;
            }

            if(this.endpoint == ''){
                this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                return;
            }
            let obj = {};
            obj.pkgname = containername;
            obj.version = version;
            obj.taskname = "###";
            obj.target = dockerAction[cid];
            _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerAction."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                if (willfunc) {
                    _g.openGlobalLoading();
                    setDeviceStatus(this.endpoint, dockerAction[cid], JSON.stringify(obj)).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status == "CHANGED"){
                                this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                    this.getMonitorDockers();                      
                                })
                            }else{
                                _g.handleError(res);
                            }
                        })
                    })
                }
            })
        },  


        getTimeOptions(){
            let options=[];
            let date = new Date();
            let time = "";
            let Hours =  date.getHours();
            if(Hours<10) Hours = "0"+Hours;
            let Min = date.getMinutes();
            if(Min<10) Min = "0"+Min;
            let Sec =  date.getSeconds();
            if(Sec<10) Sec = "0"+Sec;
            for (let i=0;i<=6;i++){
                time =  Hours+":"+Min+":"+Sec;
                options.unshift(time)
                Sec = Sec - this.intervalTime;
                if(Sec<10&&0<=Sec){
                    Sec = "0"+Sec;
                }else if(Sec<0){
                    Sec=60+Sec;
                    Min--;
                    if(Min<10){
                        Min = "0"+Min;
                    }
                }
            }
            return options;
        },

        drawMemoryChart(){
            let labelOptions = this.getTimeOptions();
            let option = {
                title: {
                    text: this.$t('overview.memory'),
                    subtext: parseInt(this.memoryNowPercentage)+'%',
                    textStyle: {
                        fontSize:12,
                        fontWeight: 700,
                        color: "#5cb85c"
                    },
                    subTextStyle: {
                        fontSize:9,
                    },
                    x:'center'
                },
                xAxis: {
                    type: 'category',
                    axisLabel: {
                        interval: 0,
                    },
                    data: labelOptions
                },
                yAxis: {
                    type: 'value',
                    max: 100,
                    min: 0,
                    interval: 10
                },
                series: [{
                    name: "Memory",
                    lineStyle: {
                        color: "#5cb85c"
                    },
                    data: this.memoryData,
                    type: 'line',
                }],
            };
            this.cpuChart = this.$echarts.init(document.getElementById("memorychart"));
            this.cpuChart.setOption(option)
        },

        drawCpuChart(){
            let labelOptions = this.getTimeOptions();
            let option = {
                title: {
                    text: this.$t('overview.cpu'),
                    subtext: parseInt(this.cpuNowPercentage)+'%',
                    textStyle: {
                        fontSize:12,
                        fontWeight: 700,
                        color: "#5cb85c"
                    },
                    subTextStyle: {
                        fontSize:9,
                    },
                    x:'center'
                },
                xAxis: {
                    type: 'category',
                    axisLabel: {
                        interval: 0,
                    },
                    data: labelOptions
                },
                yAxis: {
                    type: 'value',
                    max: 100,
                    min: 0,
                    interval: 10
                },
                series: [{
                    name: "CPU Usage",
                    lineStyle: {
                        color: "#5cb85c"
                    },
                    data: this.cpuData,
                    type: 'line'
                }],
            };
            this.cpuChart = this.$echarts.init(document.getElementById("cpuchart"));
            this.cpuChart.setOption(option);
        },

        startDeviceMonitor(selectedAgentId){
            if(_g.mtimer == null){
                _g.mtimer = window.setInterval(() => {
                    this.getMonitorApps();
                    this.getMonitorDockers();
                    this.getDeviceThresholdSetting();
                }, this.intervalTime*1000)
            } 
        },

        stopDeviceMonitor(){
            window.clearInterval(_g.mtimer);
            _g.mtimer = null;
            
        },

        back(){
            this.$router.go(-1);
        }
    },

    components:{
        vncDialog,
    },

    mounted(){
            this.drawCpuChart();
            this.drawMemoryChart();
        },

    created() {
        if(this.$route.query!=null){
            this.deviceName=this.$route.query.deviceName;
            this.gname=this.$route.query.gname;
            this.endpoint=this.$route.query.endpoint;
            this.os=this.$route.query.os;
            this.startDeviceMonitor(this.endpoint);
            this.getDeviceThresholdSetting();
            this.getDeviceDetails(this.endpoint);
            this.getMonitorApps();
            this.getPeripherals();
            if(this.os == groupType.linux|| this.os ==groupType.windows){
                this.getMonitorDockers();
            }
        }else{
            console.log("the devicename is null")
        }
    },

    beforeDestroy() {
        this.stopDeviceMonitor();
        this.endpoint = "";
        
    },
    watch:{
        peripheralMsgCount(){
            this.getPeripherals();
        },
    },
    computed:{
        ...mapState({
            peripheralMsgCount: "peripheralMsgCount",
        }),   
    },
}
</script>
<style lang="scss" scoped>
 @import "../../assets/css/colors"; 
    .devMess{
        font-size: 18px;
        font-weight: bolder;
        span{
            display: inline;
            font-size: 10px;
            font-weight:100;
            margin-right: 15px;

        }
    }
    .title{
        font-size: 17px;
        color: #1382c6;
        font-weight: bolder;
    }

    .action{
        font-size: 20px;
        color: #1382c6;
        font-weight: bolder;
        text-align: center;
        height: 100%;
        p{
          margin: 10px;
          text-align: center;
        }
        button{
            width: 150px; 
            border-color: #1382c6;
            border-width: 2px;
            color:#1382c6;
            font-weight: bold 
        }
    }
    .overview-content{
        display: flex;
        flex-direction: row;
        div{
            margin-left: 10px;
            width: 25%;
        }
        span{
            display: block;
            height: 10px;
        }
        margin-top: 10px;
    }
    .info-box-icon-red {
        color:red;
    }
    .info-box-icon-green {
        color:green;
    }
    .normal{
        color: #5cb85c;
        font-weight: bold; 
    }
    .warn{
        color: orange;
        font-weight: bold; 
    }
    .error{
        color: red;
        font-weight: bold; 
    }
    .black{
        color: black;
        // font-weight: bold; 
    }
    .notClick{
        color: grey;
        pointer-events: none;
    }
    canvas{
        width: 100%;
        height: 100%;
    }
    .dockerIcon{
        position: relative;
        top: 6px;
    }
    .deviceInfo{        
        margin-right: 10px; 
        border: 1px solid #ebeef5; 
        background: #fff;        
        border-radius: 5px 5px 5px 5px;  
        padding-left:10px;        
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);        
        -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);        
        -o-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);        
        .label{           
            text-align: right;            
            font-size: 14px;            
            color: #606266;            
            line-height: 50px;            
            padding: 0 12px 0 0;            
            font-weight: 600;            
            margin-left: 10px;            
            font-size: 15px;        
        }        
        .info{            
            font-size: 13px;                    
        }    
    }
</style>



